import React, { useState, useEffect, useCallback, useRef } from "react";
import { returnPlayerName } from "../../helper.js";
import ReactCheckers from "../Checker/Checker.js";
import Field from "../Field/Field.js";
import { connect } from "react-redux";
import Opponent from "../AI/AI.js";
import { local } from "d3";

function useStateCallback(initialState) {
  const [state, setState] = useState(initialState);
  const cbRef = useRef(null); // mutable ref to store current callback

  const setStateCallback = useCallback((state, cb) => {
    cbRef.current = cb; // store passed callback to ref
    setState(state);
  }, []);

  useEffect(() => {
    // cb.current is `null` on initial render, so we only execute cb on state *updates*
    if (cbRef.current) {
      cbRef.current(state);
      cbRef.current = null; // reset callback after execution
    }
  }, [state]);

  return [state, setStateCallback];
}

const setColumns = () => {
  const columns = {};
  columns.a = 0;
  columns.b = 1;
  columns.c = 2;
  columns.d = 3;
  columns.e = 4;
  columns.f = 5;
  columns.g = 6;
  columns.h = 7;

  return columns;
};
const createBoard = (columns) => {
  let board = {};

  for (let key in columns) {
    if (columns.hasOwnProperty(key)) {
      for (let n = 1; n <= 8; ++n) {
        let row = key + n;
        board[row] = null;
      }
    }
  }

  board = initPlayers(board);

  return board;
};
const initPlayers = (board) => {
  const player1 = [
    "a8",
    "c8",
    "e8",
    "g8",
    "b7",
    "d7",
    "f7",
    "h7",
    "a6",
    "c6",
    "e6",
    "g6",
  ];
  const player2 = [
    "b3",
    "d3",
    "f3",
    "h3",
    "a2",
    "c2",
    "e2",
    "g2",
    "b1",
    "d1",
    "f1",
    "h1",
  ];

  player1.forEach(function (i) {
    board[i] = createPiece(i, "player1");
  });

  player2.forEach(function (i) {
    board[i] = createPiece(i, "player2");
  });

  return board;
};

const createPiece = (location, player) => {
  let piece = {};

  piece.player = player;
  piece.location = location;
  piece.isKing = false;

  return piece;
};

const Game = ({ players, color, audio, theme }) => {
  const columns = setColumns();
  const User = new ReactCheckers(columns);
  const Computer = new Opponent(columns);
  const [state, setState] = useStateCallback({
    history: [
      {
        boardState: createBoard(columns),
        currentPlayer: color === "White" ? false : true,
      },
    ],
    activePiece: null,
    moves: [],
    jumpKills: null,
    hasJumped: null,
    stepNumber: 0,
    winner: null,
  });

  let currentState = state.history[state.stepNumber];
  let boardState = state.history[state.stepNumber].boardState;

  let currentPlayer = state.history[state.stepNumber].currentPlayer;
  let moves = state.moves;

  let gameStatus;
  let undoClass = "undo";

  if (state.stepNumber < 1) {
    undoClass += " disabled";
  }

  switch (state.winner) {
    case "player1pieces":
      gameStatus = "Black won!";
      break;
    case "player2pieces":
      gameStatus = "White won!";
      break;
    case "player1moves":
      gameStatus = "No moves left. Black won!";
      break;
    case "player2moves":
      gameStatus = "No moves left. White won!";
      break;
    default:
      gameStatus =
        currentState.currentPlayer === true
          ? "Black's turn."
          : "White's turn.";
      break;
  }

  const getCurrentState = () => {
    const history = state.history.slice(0, state.stepNumber + 1);
    return history[history.length - 1];
  };

  const handleClick = async (coordinates) => {
    if (state.winner !== null) {
      return;
    }
    const currentState = getCurrentState();
    const boardState = currentState.boardState;
    const clickedSquare = boardState[coordinates];

    // Clicked on a piece
    if (clickedSquare !== null) {
      // Can't select opponents pieces
      if (
        clickedSquare.player !== returnPlayerName(currentState.currentPlayer)
      ) {
        return;
      }

      // Unset active piece if it's clicked
      if (state.activePiece === coordinates && state.hasJumped === null) {
        setState({
          ...state,
          activePiece: null,
          moves: [],
          jumpKills: null,
        });

        // boardState=currentState.boardState;
        return;
      }

      // Can't choose a new piece if player has already jumped.
      if (state.hasJumped !== null && boardState[coordinates] !== null) {
        return;
      }

      // Set active piece
      let movesData = User.getMoves(
        boardState,
        coordinates,
        clickedSquare.isKing,
        false
      );

      setState({
        ...state,
        activePiece: coordinates,
        moves: movesData[0],
        jumpKills: movesData[1],
      });
      //boardState=currentState.boardState;
      return;
    }

    // Clicked on an empty square
    if (state.activePiece === null) {
      return;
    }

    // Moving a piece
    if (state.moves.length > 0) {
      const postMoveState = User.movePiece(coordinates, state, color);

      if (postMoveState === null) {
        return;
      }
      audio.play();
      updateStatePostMove(postMoveState).then((res) => {
        if (
          postMoveState.currentPlayer === !currentPlayer &&
          postMoveState.winner === null
        ) {
          computerTurn(null, res);
        }
      });
    }
  };

  const computerTurn = (piece = null, curState) => {
    if (players > 1) {
      return;
    }

    setTimeout(() => {
      const curHistory = curState.history.slice(0, curState.stepNumber + 1);

      const boardState = curHistory[curHistory.length - 1].boardState;

      let computerMove;
      let coordinates;
      let moveTo;

      // If var piece != null, the piece has previously jumped.
      if (piece === null) {
        //computerMove = Computer.getRandomMove(boardState, "player2");
        computerMove = Computer.getSmartMove(
          curState,
          boardState,
          color == "Black" ? "player2" : "player1"
        );

        coordinates = computerMove.piece;
        moveTo = computerMove.moveTo;
      } else {
        // Prevent the computer player from choosing another piece to move. It must move the active piece
        computerMove = User.getMoves(
          boardState,
          piece,
          boardState[piece].isKing,
          true
        );
        coordinates = piece;
        moveTo =
          computerMove[0][Math.floor(Math.random() * computerMove[0].length)];
      }

      const clickedSquare = boardState[coordinates];

      let movesData = User.getMoves(
        boardState,
        coordinates,
        clickedSquare.isKing,
        false
      );

      setState(
        {
          ...curState,
          activePiece: coordinates,
          moves: movesData[0],
          jumpKills: movesData[1],
        },
        (res) => {
          setTimeout(() => {
            const postMoveState = User.movePiece(moveTo, res, color);

            if (postMoveState === null) {
              return;
            }
            audio.play();
            updateStatePostMove(postMoveState).then((result) => {
              if (postMoveState.currentPlayer === false && color === "Black") {
                computerTurn(postMoveState.activePiece, result);
              }
            });
          }, 500);
        }
      );
    }, 1000);
  };

  const updateStatePostMove = (postMoveState) => {
    return new Promise((res, rej) => {
      setState(
        {
          ...state,
          history: [
            ...state.history,
            {
              boardState: postMoveState.boardState,
              currentPlayer: postMoveState.currentPlayer,
            },
          ],
          activePiece: postMoveState.activePiece,
          moves: postMoveState.moves,
          jumpKills: postMoveState.jumpKills,
          hasJumped: postMoveState.hasJumped,
          stepNumber: state.history.length,
          winner: postMoveState.winner,
        },
        res
      );
    });
  };

  const undo = () => {
    setState({
      history: [
        {
          boardState: createBoard(columns),
          currentPlayer: color === "White" ? false : true,
        },
      ],
      activePiece: null,
      moves: [],
      jumpKills: null,
      hasJumped: null,
      stepNumber: 0,
      winner: null,
    });
    localStorage.setItem(
      "game-state",
      JSON.stringify({
        history: [
          {
            boardState: createBoard(columns),
            currentPlayer: color === "White" ? false : true,
          },
        ],
        activePiece: null,
        moves: [],
        jumpKills: null,
        hasJumped: null,
        stepNumber: 0,
        winner: null,
      })
    );
  };

  useEffect(() => {
    if (localStorage.getItem("game-state")) {
      console.log(state);
      setState(JSON.parse(localStorage.getItem("game-state")));
    }
    if (state.history[0].currentPlayer) {
      computerTurn(null, state);
    }
  }, []);

  useEffect(() => {
    localStorage.setItem("game-state", JSON.stringify(state));
  });

  return (
    <div className="reactCheckers">
      <div className="game-status">{gameStatus}</div>
      <div
        className={
          theme == "1"
            ? "game-board-wooden"
            : theme == "0"
            ? "game-board"
            : "game-board-cartoon"
        }
      >
        <div className="game-board-letter">
          <span>8</span>
          <span>7</span>
          <span>6</span>
          <span>5</span>
          <span>4</span>
          <span>3</span>
          <span>2</span>
          <span>1</span>
        </div>
        <Field
          boardState={boardState}
          currentPlayer={currentPlayer}
          activePiece={state.activePiece}
          moves={moves}
          columns={columns}
          onClick={(coordinates) => handleClick(coordinates)}
        />
        <div className="game-board-number">
          <span>A</span>
          <span>B</span>
          <span>C</span>
          <span>D</span>
          <span>E</span>
          <span>F</span>
          <span>G</span>
          <span>H</span>
        </div>
      </div>
      <div className="time-travel">
        <button className={undoClass} onClick={() => undo()}>
          Restart
        </button>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  players: state.app.players,
  color: state.app.color,
  theme: state.app.theme,
});

export default connect(mapStateToProps)(Game);
